}

#search-box {
  font: inherit;
  max-width: 400px;
  padding: 8px;
}

p, h1, h2, ul {
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.section {
  padding-top: 30px;
